import React, { Suspense, lazy } from 'react';
import {BrowserRouter as Router,Redirect,Switch,Route} from "react-router-dom";
import { ToastContainer} from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import '../App.scss';
import Sticky from './Sticky';
const Home = lazy(() => import('./Home'));
const NotFound = lazy(() => import("./NotFound"));
const Login = lazy(() => import("./LoginPage/Login"));
const Register = lazy(() => import("./LoginPage/Register"));
const PostProperty = lazy(() => import("./property/PostProperty"));
const PostPropertyimg = lazy(() => import("./property/PostPropertyimg"));
const Rent = lazy(() => import("./property/Rent"));
const Buy = lazy(() => import("./property/Buy"));
const Pg = lazy(() => import("./property/Pg"));
const SearchHome = lazy(() => import("./search/SearchHome2"));
const PropertyView = lazy(() => import("./property/PropertyView"));
const PostPropertyPG = lazy(() => import("./property/PostPropertyPG"));
const Postpgimg = lazy(() => import("./property/Postpgimg"));
const PropertyEdit = lazy(() => import("./property/PropertyEdit"));
const PropertyimgEdit = lazy(() => import("./property/PropertyimgEdit"));
const PropertyViewPG = lazy(() => import("./property/PropertyViewPG"));
const PropertyPGEdit = lazy(() => import("./property/PropertyPGEdit"));
const Postpgimgedit = lazy(() => import("./property/Postpgimgedit"));
const Propertyimgorder = lazy(() => import("./property/Propertyimgorder"));
const Propertyimgorderpg = lazy(() => import("./property/Propertyimgorderpg"));
const AccountAll = lazy(() => import("./myaccount/AccountAll"));

const SearchPage = lazy(() => import("./search/SearchPage"));



function Dashboard() {
  return (<>   <ToastContainer />
    <Router basename="/">
      <Suspense fallback={<div>Loading...</div>}>
        <Switch>
         <Route exact path="/" component={Home}/>
         <Route exact path="/Login" component={Login}/>
         <Route exact path="/Register" component={Register}/>
         <Route exact path="/PostProperty" component={PostProperty}/>
         <Route exact path="/PostPropertyimg/:AddPreview" component={PostPropertyimg}/>
         <Route exact path="/Rent" component={Rent}/>
         <Route exact path="/Buy" component={Buy}/>
         <Route exact path="/Pg" component={Pg}/>
         <Route exact path="/SearchHome" component={SearchHome}/>
         <Route exact path="/PropertyView/:AddPreview" component={PropertyView}/>
         <Route exact path="/PostPropertyPG" component={PostPropertyPG}/>
         <Route exact path="/Postpgimg/:AddPreview" component={Postpgimg}/>
         <Route exact path="/AccountAll" component={AccountAll}/>
         <Route exact path="/PropertyEdit/:AddPreview" component={PropertyEdit}/>
         <Route exact path="/PropertyimgEdit/:AddPreview" component={PropertyimgEdit}/>
         <Route exact path="/PropertyViewPG/:AddPreview" component={PropertyViewPG}/>
         <Route exact path="/PropertyPGEdit/:AddPreview" component={PropertyPGEdit}/>
         <Route exact path="/Postpgimgedit/:AddPreview" component={Postpgimgedit}/>
         <Route exact path="/Propertyimgorder/:AddPreview" component={Propertyimgorder}/>
         <Route exact path="/Propertyimgorderpg/:AddPreview" component={Propertyimgorderpg}/>
         <Route exact path="/SearchPage/:AddPreview" component={SearchPage}/>
         <Route exact path="/404" component={NotFound} />
          <Redirect to="/404" />

       
        </Switch>
      </Suspense>
    </Router>
    <Sticky />
    </>
  );
}
export default Dashboard;
